import React, { useState } from "react";
import "../styles/contact.css";

const Contact = () => {
  const [enteredEmail, setEnteredEmail] = useState("");
  const [enteredName, setEnteredName] = useState("");
  const [enteredPhone, setEnteredPhone] = useState("");
  const [enteredMessage, setEnteredMessage] = useState("");

  // const [userInput, setUserInput] = useState({
  //   enteredEmail: '',
  //   enteredName: '',
  //   enteredPhone: '',
  //   enteredMessage: ''
  // });

  const emailHandler = (e) => {
    setEnteredEmail(e.target.value);
  };

  const nameHandler = (e) => {
    setEnteredName(e.target.value);
  };

  const phoneHandler = (e) => {
    setEnteredPhone(e.target.value);
  };

  const messageHandler = (e) => {
    setEnteredMessage(e.target.value);
  };

  const submitHandler = (e) => {
    e.preventDefault();
    const contactData = {
      email: enteredEmail,
      name: enteredName,
      phone: enteredPhone,
      message: enteredMessage,
    };
    console.log(contactData);

    setEnteredEmail("");
    setEnteredName("");
    setEnteredPhone("");
    setEnteredMessage("");
  };

  return (
    <div className="contact" id="contact">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 230">
        <path
          fill="#eeeeee"
          fill-opacity="1"
          d="M0,160L60,144C120,128,240,96,360,80C480,64,600,64,720,74.7C840,85,960,107,1080,133.3C1200,160,1320,192,1380,208L1440,224L1440,0L1380,0C1320,0,1200,0,1080,0C960,0,840,0,720,0C600,0,480,0,360,0C240,0,120,0,60,0L0,0Z"
        ></path>
      </svg>{" "}
      <div>
        <h3>Contacts</h3>
        <hr></hr>
        <p>Have a question or want to work together?</p>
        <div className="my-contact">
          <a href="tel:+1-514-247-1415">Click to call Us!</a>
          <span> | </span>
          <a href="mailto: dhdhariaajit@gmail.com"> dhdhariaajit@gmail.com</a>
        </div>
      </div>
      <form className="contact-form" onSubmit={submitHandler}>
        <div className="form-content">
          <div className="user-details">
            <div className="mb-2">
              <input
                type="email"
                className="form-control"
                id="contactEmail"
                placeholder="Your Email"
                value={enteredEmail}
                onChange={emailHandler}
              />
            </div>
            <div className="mb-2">
              <input
                type="text"
                className="form-control"
                id="contactName"
                placeholder="Your Name"
                value={enteredName}
                onChange={nameHandler}
              />
            </div>
            <div className="mb-2">
              <input
                type="text"
                className="form-control"
                id="contactPhone"
                placeholder="Your Mobile"
                value={enteredPhone}
                onChange={phoneHandler}
              />
            </div>
          </div>
          <div className="mb-1">
            <input
              type="text"
              className="form-control message"
              id="contactMessage"
              placeholder="Your Message"
              value={enteredMessage}
              style={{ textAlign: "center" }}
              onChange={messageHandler}
            />
          </div>
        </div>
        <button type="submit" className="btn submit-button">
          Submit
        </button>
      </form>
    </div>
  );
};

export default Contact;
